@Component({
  selector: "app-material-information",
  templateUrl: "./material-information.component.html",
  styleUrls: ["./material-information.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MaterialInformationComponent
  implements OnInit, OnChanges, AfterViewChecked {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<S1Application>;
  // @ViewChild(AttachmentDocumentComponent, { static: false }) materialAttachmentDocument: AttachmentDocumentComponent;

  @Output()
  checkSingleMediaChangesFunction: EventEmitter<any> = new EventEmitter();
  @Output()
  checkMultimediaChangesFunction: EventEmitter<any> = new EventEmitter();

  // @ViewChild('mainFile')
  // myMainFile: ElementRef;
  // @ViewChild('mainInput')
  // myMainInput: ElementRef;
  // @ViewChild('supportiveFile')
  // mySupportiveFile: ElementRef;
  // @ViewChild('revisionLetterFile')
  // myRevisionLetterFile: ElementRef;

  @Input() s1Application = new S1Application();
  @Input() s2Application = new S2Application();
  @Input() originalAttachmentDocuments = [];
  @Input() revisionAttachmentDocuments = [];
  @Input() complianceAttachmentDocuments = [];
  @Input() revisionLetterAttachment: any;
  @Input() newMultimediaList = [];
  @Input() typeOfMediumList = []; // single and multimedia
  @Input() supportiveDocumentList = [];
  @Input() applicationSingleMedia = new ApplicationSingleMedia();
  @Input() isAscUser = false;
  @Input() isDisableFieldOnUpdate = false;

  dataSource: MatTableDataSource<S1Application>;
  displayedColumns: string[];
  todayDate: Date = new Date();
  materialInformationForm: FormGroup;

  executionList = [];
  dialectList = [];
  newSupportiveDocumentList = [];
  typeOfDocumentList = [];
  specialApplicationType = [...SPECIAL_APPLICATION];

  setMultimediaList = [];
  @Input() selectedMultimediaList = [];
  selectedMultimediaStringList = [];
  @Input() toDeleteMultimediaList = [];
  // singleMedia = new SingleMedia();
  typeOfMedium = new TypeOfMedium();
  typeOfDocument = new TypeOfDocument();
  // attachmentDocument = new AttachmentDocument();
  supportingDocument = new SupportingDocument();
  applicationMultimedia = new ApplicationMultimedia();
  applicationRequest = new ApplicationRequest();
  newApplicationSingleMedia = new ApplicationSingleMedia();

  selectMultimedia = false;
  // showMainDocumentUploadButton = false;
  // showMainDocumentViewButton = false;
  // showMainDocumentDownloadButton = false;
  // showSupportDocumentFrame = false;
  // showMainDocumentRemoveButton = false;
  // showRevisionLetterViewButton = false;

  previewMainDocument = false;
  disableSubmitButtonByMedia = false;

  showSupportiveDocumentUploadButton = false;
  showSupportiveDocumentViewButton = false;
  showSupportiveDocumentDownloadButton = false;
  showRevisionLetterDownloadButton = false;
  previewSupportiveDocument = false;

  attachmentSize: number;
  safeURLMainDoc: any;
  safeURLSupportDoc: any;
  currentUser: any;
  applicationStage: string;
  safeURLRevisionLetter: any;
  supportingDocumentIndex: any;
  showRevisionLetterUploadButton = false;
  isDigitalStatic = false;
  isUpdate = false;

  // validations
  hiddenValidation: string;
  readonlyValidation: string;
  disabledValidation: string;

  attachmentType = ATTACHMENT_TYPE.MAIN;
  selectedFiles: FileList;
  currentFileUpload: File;
  progress: { percentage: number } = { percentage: 0 };
  uploadedFileMain: any;
  uploadedFileSupportive: any;
  uploadedFileRevision: any;
  mediaType = "";
  labelMainDocument = "MAIN DOCUMENT";
  @Input() savedTypeOfMedium = "";
  private ngUnsubscribe = new Subject();
  selectedTypeOfMedium = "SINGLE MEDIA";
  @Input() multimediaDTO: any = {};
  multimediaMovingDTO = new ApplicationMultimediaMovingDTO();
  multimediaStaticDTO = new ApplicationMultimediaStaticDTO();

  constructor(
    private formBuilder: FormBuilder,
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    private sanitizer: DomSanitizer,
    public dataStorage: DataStorageService,
    private fileStorageService: FileStorageService,
    private cd: ChangeDetectorRef,
    public validateFieldService: ValidateFieldService,
    private spinner: NgxSpinnerService,
    public util: CommonServices
  ) {}

  ngOnInit(): void {
    console.log(this.originalAttachmentDocuments);
    this.currentUser = this.dataStorage.getUserAccount();
    if (this.currentUser.userRole.name === USER_ROLES.ROLE_AD_SPECIALIST) {
      this.displayedColumns = [
        "typeOfDocument",
        "input",
        "variant",
        "expiresOn",
        "basedOn",
        "remarks",
        "action",
      ];
    } else {
      this.displayedColumns = [
        "typeOfDocument",
        "input",
        "variant",
        "expiresOn",
        "basedOn",
        "remarks",
      ];
    }

    this.setFormValidator();
    this.getTypeOfDocumentList();

    if (this.dataStorage.getSpecialS1Application()) {
      this.applicationRequest = this.dataStorage.getSpecialS1Application();
    }
    this.emitSingleMediaChanges();
    this.initializeMultimediaMovingObj();
    this.initializeMultimediaStaticObj();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes) {
      if (this.multimediaDTO) {
        if (
          this.s1Application.refTypeOfMedium ===
          TYPE_OF_MEDIUM.MULTIMEDIA_MOVING
        )
          this.multimediaMovingDTO = this.multimediaDTO;
        if (
          this.s1Application.refTypeOfMedium ===
          TYPE_OF_MEDIUM.MULTIMEDIA_STATIC
        )
          this.multimediaStaticDTO = this.multimediaDTO;
      }
    }

    if (this.s1Application.refTypeOfMedium === undefined) {
      this.s1Application.refTypeOfMedium = TYPE_OF_MEDIUM.SINGLE_MEDIA;
      this.getTypeOfMedium(TYPE_OF_MEDIUM.SINGLE_MEDIA);
    } else if (
      !this.applicationSingleMedia ||
      this.s1Application.refTypeOfMedium?.includes("MULTIMEDIA")
    ) {
      this.getTypeOfMedium(this.s1Application.refTypeOfMedium);
    } else if (this.applicationSingleMedia) {
      if (this.applicationSingleMedia.mediumExecution) {
        this.typeOfMedium = this.applicationSingleMedia.mediumExecution.typeOfMedium;
        if (this.typeOfMedium.id) {
          this.getExecutionDialectList();
        }
      }
    }
  }

  ngAfterViewChecked() {
    this.cd.detectChanges();
  }

  setFormValidator() {
    this.materialInformationForm = this.formBuilder.group({
      adTitle: ["", Validators.required],
      referenceCode: [""],
      previousRefCode: [""],
      tagLine: [""],
      refTypeOfMedium: [""],
      appFormReference: [""],

      // this.attachmentDocumentForm = this.formBuilder.group({
      attachmentReference: [""],
      hasNoSupportingDocs: [""],
      typeOfDocument: [""],
      basedOn: [""],
      variant: [""],
      dateOfLaunch: [""],
      expiresOn: [""],
      remarks: [""],
      input: [""],

      typeOfMedium: [""],
      mediumExecution: [""],
      language: [""],
      dialect: [""],
      others: [""],
      sizeLength: [""],
      width: ["0"],

      multimediaLength: [""],
      multimediaSize: [""],
      applicationSingleMedia: [""],
      applicationMultimediaStatic: [""],
      applicationMultimediaMoving: [""],
    });
  }

  getTypeOfMedium(type) {
    this.typeOfMediumList = [];
    let endpoint = "";
    if (type === TYPE_OF_MEDIUM.SINGLE_MEDIA) {
      this.applicationSingleMedia = new ApplicationSingleMedia();
      endpoint = ENDPOINTS.typeOfMediumSingleMedia;
      this.selectMultimedia = false;
      this.s1Application.refTypeOfMedium = TYPE_OF_MEDIUM.SINGLE_MEDIA;
      this.selectedTypeOfMedium = TYPE_OF_MEDIUM.SINGLE_MEDIA;
    } else if (type === TYPE_OF_MEDIUM.MULTIMEDIA_MOVING) {
      endpoint = ENDPOINTS.typeOfMediumMultiMediaMoving;
      this.selectMultimedia = true;
      this.s1Application.refTypeOfMedium = TYPE_OF_MEDIUM.MULTIMEDIA_MOVING;
      this.initializeMultimediaStaticObj();
      this.selectedTypeOfMedium = TYPE_OF_MEDIUM.MULTIMEDIA;
    } else {
      endpoint = ENDPOINTS.typeOfMediumMultiMediaStatic;
      this.selectMultimedia = true;
      this.s1Application.refTypeOfMedium = TYPE_OF_MEDIUM.MULTIMEDIA_STATIC;
      this.initializeMultimediaMovingObj();
      this.selectedTypeOfMedium = TYPE_OF_MEDIUM.MULTIMEDIA;
    }

    // console.log('Selected: ', this.selectedTypeOfMedium)
    this.apiService.findAll(endpoint).subscribe(
      (res: any) => {
        this.typeOfMediumList = res.responseData.data;
        if (type === TYPE_OF_MEDIUM.SINGLE_MEDIA) {
          this.typeOfMedium = this.typeOfMediumList.filter(
            (medium) => medium.description === this.savedTypeOfMedium
          )[0];
        }

        // if (this.selectedMultimediaList.length > 0) {
        //   this.selectedMultimediaStringList = this.selectedMultimediaList.map(mappedList => mappedList?.typeOfMedium?.description);
        //   this.emitMultimediaChanges();
        // }
        // this.typeOfMediumList = this.typeOfMediumList.map((medium, index) => {
        //   const sampleList = this.selectedMultimediaList.filter(filteredMedium => filteredMedium.typeOfMedium.id === medium.id);
        //   if (sampleList.length) {
        //     medium.sizeLength = sampleList[0].multimediaSizeLength;
        //   } else {
        //     medium.sizeLength = null;
        //   }
        //   return medium;
        // });
      },
      (err: any) => {}
    );
  }

  resetValues() {
    this.applicationSingleMedia.dialect = null;
    this.applicationSingleMedia.language = null;
    this.applicationSingleMedia.others = "";
    this.applicationSingleMedia.sizeLength = "0";
    this.applicationSingleMedia.amount = 0;
    this.applicationSingleMedia.mediumExecution = null;
    this.applicationSingleMedia.width = "0";
  }

  setSingleMedia(typeofMedium: any) {
    this.resetValues();
    this.typeOfMedium = typeofMedium;
    this.labelMainDocument = this.typeOfMedium.typeOfMainDocument.typeOfMainDocument;
    this.getExecutionDialectList();
    this.emitSingleMediaChanges();
  }

  emitSingleMediaChanges() {
    if (
      (this.typeOfMedium?.executionEnable &&
        !this.applicationSingleMedia.mediumExecution) ||
      (this.typeOfMedium?.lengthEnable &&
        !this.applicationSingleMedia.sizeLength) ||
      (this.typeOfMedium?.sizeLengthEnable &&
        !this.applicationSingleMedia.sizeLength) ||
      (!this.typeOfMedium?.id &&
        !this.applicationSingleMedia.id &&
        !this.savedTypeOfMedium)
    ) {
      this.disableSubmitButtonByMedia = true;
    } else {
      this.disableSubmitButtonByMedia = false;
    }

    this.checkSingleMediaChangesFunction.emit();
  }

  // addMultimedia(obj: any, evt) {
  //   if (evt.target.value) {
  //     obj.sizeLength = evt.target.value;
  //     const existingValue = this.newMultimediaList.filter(filteredList => filteredList === obj);
  //     if (existingValue.length) {
  //       existingValue[0] = obj;
  //     } else {
  //       this.newMultimediaList.push(obj);
  //       this.selectedMultimediaList.push(obj);
  //     }
  //     if (obj?.id) {
  //       this.toDeleteMultimediaList = this.toDeleteMultimediaList.filter(filtered => filtered.id !== obj?.id);
  //     }
  //   } else {
  //     const deleteExisting = this.selectedMultimediaList.filter(filteredMedia => filteredMedia?.typeOfMedium?.id === obj?.id);
  //     if (deleteExisting.length) {
  //       this.toDeleteMultimediaList.push(deleteExisting[0]);
  //     }

  //     this.newMultimediaList = this.newMultimediaList.filter(filteredMultimedia => filteredMultimedia.id !== obj.id);
  //     this.selectedMultimediaList = this.selectedMultimediaList.filter(filteredMultimedia => filteredMultimedia.id !== obj.id);
  //   }
  //   const toDeleteMultimediaSet = this.toDeleteMultimediaList.filter((item, i, ar) => ar.indexOf(item) === i);
  //   this.toDeleteMultimediaList = toDeleteMultimediaSet;
  //   this.emitMultimediaChanges();
  // }

  emitMultimediaChanges() {
    this.disableSubmitButtonByMedia = false;
    let data = {
      type: this.s1Application.refTypeOfMedium,
      data:
        this.s1Application.refTypeOfMedium === TYPE_OF_MEDIUM.MULTIMEDIA_MOVING
          ? this.multimediaMovingDTO
          : this.multimediaStaticDTO,
    };
    this.checkMultimediaChangesFunction.emit(data);
  }

  getTypeOfDocumentList() {
    this.apiService.findAll(ENDPOINTS.typeOfDocument).subscribe(
      (res: any) => {
        this.typeOfDocumentList = res.responseData.data;
      },
      (err: any) => {}
    );
  }

  setTypeOfDocument(type) {
    this.typeOfDocument = type;
  }

  compareSingleMedia(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  compareMediumExecution(value: any, selected: any): boolean {
    if (value && selected) {
      return (
        value.typeOfExecution.typeOfExecution ===
        selected.typeOfExecution.typeOfExecution
      );
    }
  }

  compareDialect(value: any, selected: any): boolean {
    if (value && selected) {
      return value.dialect === selected.dialect;
    }
  }

  getExecutionDialectList() {
    if (this.typeOfMedium.id) {
      this.apiService
        .findAll(`${ENDPOINTS.typeOfExecutionsByMedium}${this.typeOfMedium.id}`)
        .subscribe(
          (res: any) => {
            if (this.typeOfMedium.executionEnable) {
              this.executionList = res.responseData.data;
            } else {
              if (res.responseData.data.length === 0) {
                this.sweetAlertService.customErrorMessage(
                  "The selected type of medium has no execution."
                );
              } else {
                this.applicationSingleMedia.mediumExecution =
                  res.responseData.data[0];
              }
            }
            this.emitSingleMediaChanges();
          },
          (err: any) => {}
        );
    }

    this.apiService.findAll(ENDPOINTS.dialects).subscribe(
      (res: any) => {
        this.dialectList = res.responseData.data;
      },
      (err: any) => {}
    );
  }

  removeTypeOfDocument(index) {
    const keepValues = [];

    Swal.fire({
      text: "Are you sure you want to delete this TYPE OF DOCUMENT?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirm",
    }).then((result) => {
      if (result.value) {
        this.supportiveDocumentList.forEach((element, key) => {
          if (key !== index) {
            keepValues.push(element);
          }
        });
        this.supportiveDocumentList = keepValues;
        this.dataSource.data = keepValues;
      }
    });
  }

  addSupportiveDocument(supportingDocument) {
    const checkIfDocumentAlreadyExist = this.supportiveDocumentList.filter(
      (data) => data.typeOfDocument.id === supportingDocument.typeOfDocument.id
    );

    // if (checkIfDocumentAlreadyExist.length === 1) {
    //   this.sweetAlertService.customErrorMessage('Type of Document that is being added is already on the list.', 5);
    // } else {
    this.newSupportiveDocumentList.push(supportingDocument);
    this.supportiveDocumentList.push(supportingDocument);
    this.dataSource = new MatTableDataSource<S1Application>(
      this.supportiveDocumentList
    );
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    // }
    this.supportingDocument = new SupportingDocument();
    this.typeOfDocument = new TypeOfDocument();
  }

  getTypeOfDocument(res, index) {
    this.isUpdate = true;
    this.apiService
      .findById(ENDPOINTS.typeOfDocument, res.typeOfDocument.id)
      .subscribe((data: any) => {
        if (data) {
          this.typeOfDocument = data.responseData.data;
        }
      });
    this.supportingDocument.typeOfDocument = res.typeOfDocument;
    if (res.typeOfDocument.description === "DOH-FDA PERMIT") {
      this.typeOfDocument.expiresOnEnable = true;
      this.supportingDocument.expiresOn = res.expiresOn;
      this.supportingDocumentIndex = index;
    } else if (res.typeOfDocument.description === "NMIS") {
      this.typeOfDocument.expiresOnEnable = false;
      this.supportingDocument.expiresOn = null;
      this.supportingDocumentIndex = index;
    } else if (res.typeOfDocument.description === "DOLE PERMIT") {
      this.typeOfDocument.expiresOnEnable = false;
      this.supportingDocument.expiresOn = null;
      this.supportingDocumentIndex = index;
    }
  }

  modifyTypeOfDocument(res, id) {
    const checkIfDocumentAlreadyExist = this.supportiveDocumentList.filter(
      (data) => data.typeOfDocument.id === res.typeOfDocument.id
    );
    //  if(checkIfDocumentAlreadyExist.length === 1){
    //   console.log("ALREADY EXIST");
    //  }else{
    //   console.log("SUCCESSFULLY UPDATE");
    //   this.supportiveDocumentList.forEach((element, key) => {
    //     if (key === id) {
    //       this.supportiveDocumentList[key] = res;
    //     }
    //   });
    //  }
    // console.log("count", checkIfDocumentAlreadyExist);
    // if (checkIfDocumentAlreadyExist.length === 1) {
    //   this.isUpdate = false;
    //   this.supportiveDocumentList.forEach((element, key) => {
    //     if (key === id) {
    //       this.supportiveDocumentList.splice(key);
    //     }
    //   });
    // } else {
    //   this.sweetAlertService.customErrorMessage(`Type of Document already exist.`);
    // }
    this.isUpdate = false;
    this.dataSource.data = this.supportiveDocumentList;
    this.supportingDocument = new SupportingDocument();
    this.supportingDocumentIndex = "";
  }

  initializeMultimediaMovingObj(): void {
    this.multimediaMovingDTO = new ApplicationMultimediaMovingDTO();
    let multimediaMovingList = new MultiMediaMovingConstant().data;
    this.multimediaMovingDTO = {
      applicationMultimediaMovingList: multimediaMovingList,
      appFormReference: "",
      amount: 0,
      id: 0,
      sizeLength: 0,
      typeOfMedium: null,
    };
  }

  initializeMultimediaStaticObj(): void {
    this.multimediaStaticDTO = new ApplicationMultimediaStaticDTO();
    let multimediaStaticList = new MultiMediaStaticConst().data;
    this.multimediaStaticDTO = {
      applicationMultimediaStaticList: multimediaStaticList,
      appFormReference: "",
      amount: 0,
      id: 0,
      typeOfMedium: null,
    };
  }

  isNumber(number): boolean {
    if (number) return this.validateFieldService.numbersOnly(number);
  }

  ngOnDestroy(): void {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
